	module.exports = {"data":{"markdownRemark":{"html":"<h3>Security Inbox</h3>\n<p>We’ve redesigned the security inbox to enable faster bug processing for programs. The new inbox enables programs to open reports inline so you don’t have to click backward and forward to navigate between reports.</p>\n<h3>Dashboard</h3>\n<p>The new dashboard enables insight into your security response posture. This enables programs to be on top of response time, stale issues, pending disclosures and more.</p>\n<h3>Bulk Actions</h3>\n<p>We improve our bulk actions functionality so that it’s easier to apply the same action to multiple reports with a single click.</p>\n<h3>Keyboard Shortcuts</h3>\n<p>We introduce keyboard shortcuts to make the workflow more efficient with a faster navigation.</p>\n<h3>Search</h3>\n<p>Our new inbox  filtering search functionality i enables you to quickly target the bug you’re looking for without have to scroll through your inbox.</p>\n<h3>New Integrations</h3>\n<p>We introduce these new integrations with HackerOne: <li>MantisBT</li><li>Bugzilla</li><li>Jira</li><li>Phabricator</li></p>","frontmatter":{"path":"/changelog/2014/august","title":"August 2014"}}},"pathContext":{}}

